// Reusuable classes

.container {
  max-width: 1300px;
  height: 100%;
  margin: 0 auto;
}

.block-title {
  font-family: "Playfair Display", sans-serif;
  font-weight: 700;
  font-size: 38px;
  line-height: 51px;
}

.block-pretitle {
  font-size: 23px;
  line-height: 36px;

  letter-spacing: 0.01em;
}

.block-text {
  font-size: 20px;
  line-height: 35px;

  letter-spacing: 0.01em;
}

.block-head {
  text-align: center;
}

.logo {
        display: flex;

        .logo-text {
          margin-right: 8px;
          max-width: 130px;

          font-size: 18px;
          line-height: 22px;
          font-weight: 700;
        }

        &::after {
          content: url("../images/logo.png");
          width: 42px;
          height: 42px;
    }
}

.title-md {
  margin-bottom: 10px;

  font-family: "Playfair Display", sans-serif;
  font-weight: 700;
  font-size: 28px;
  line-height: 37px;
  letter-spacing: 0.04em;
}

.title-md-bold {
  font-weight: 700;
  font-size: 25px;
  line-height: 33px;
  letter-spacing: 0.04em;
}

